#features {
    background-color: #000000;
    color: #fff;
    overflow: hidden;
    position: relative;
  }
  
  #features .overlay{
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0px;
    left: 0px;
    /* background: #000000; */
    opacity: 0.8;
    filter: alpha(opacity=90);
  }
  #features .section-heading h2{
    font-size: 28px;
    font-weight: 600;
  }
  
  #features .section-title {
    color: #fff;
    font-size: 28px;
    margin-top: 0;
    text-transform: uppercase;
    font-weight: 600;
    position: relative;
  }
  #features .section-header {
    color: #fff;
    margin-bottom: 40px;
    margin-top: 50px;
    text-align: center;
  }
  #features .section-subtitle {
    color: #fff !important;
    position: relative;
  }
  
  #features .icon {
    display: inline-block;
    width: 60px;
    height: 60px;
    border-radius: 30px;
    text-align: center;
    position: relative;
    z-index: 1;
  }
  
  #features .box-item {
    padding: 15px 0;
  }

  #features .posi-re {
    padding: 15px 0;
  }
  
  #features .box-item .icon {
    background-color: #fff;
    text-align: center;
    float: left;
    /* color: #FFC117; */
    color: #000000;
    border: 1px solid #ffffff;
    border-radius: 50px;
    -webkit-transition: all 0.2s linear;
    -moz-transition: all 0.2s linear;
    -o-transition: all 0.2s linear;
    transition: all 0.2s linear;
  }
  
  #features .box-item .icon i {
    font-size: 42px;
    line-height: 60px;
    -webkit-transition: all 0.2s linear;
    -moz-transition: all 0.2s linear;
    -o-transition: all 0.2s linear;
    transition: all 0.2s linear;
  }
  
  #features .box-item .text {
    padding-left: 80px;
  }
  
  #features .text h4 {
    color: #fff;
    font-weight: 700;
    font-size: 16px;
    text-transform: uppercase;
    line-height: 22px;
    position: relative;
  }
  
  #features .box-item .text p {
    font-size: 14px;
    line-height: 26px;
    position: relative;
  }
  
  #features .show-box img {
    position: absolute;
    bottom: -120px;
  }

  #features .lines {
    margin: auto;
    width: 70px;
    position: relative;
    border-top: 2px solid #ffffff;
    margin-top: 15px;
    margin-bottom: 15px;
  }

  #features .section {
    padding-top: 100px;
    padding-bottom: 100px;
  }