@media(min-width:568px){
    .end{
        margin-left: auto;
    }
}

@media(max-width:768px){
    #post{
        width: 100%;
    }
}



#userscomments .comments{
    margin-top: 5%;
    margin-left: 20px;
}

#userscomments .darker{
    border: 1px solid #ecb21f;
    background-color: #e0e0e0ed;
    float: right;
    border-radius: 5px;
    padding-left: 40px;
    padding-right: 30px;
    padding-top: 10px;
}

#userscomments .comment{
    /* border: 1px solid rgba(16, 46, 46, 1); */
    /* border: 1px solid #ecb21f; */
    background-color: rgb(249, 249, 249);
    float: left;
    border-radius: 5px;
    padding-left: 40px;
    padding-right: 30px;
    padding-top: 10px;
    
}
#userscomments .comment h4,.comment span,.darker h4,.darker span{
    display: inline;
}

#userscomments .comment p,.comment span,.darker p,.darker span{
    color: rgb(0, 0, 0);
}

#userscomments h1,h4{
    color: rgb(0, 0, 0);
    font-weight: bold;
}
#userscomments label{
    color: rgb(0, 0, 0);
}

#align-form{
    margin-top: 20px;
}
#userscomments .form-group p a{
    color: rgb(0, 0, 0);
}

#darker img{
    margin-right: 15px;
    position: static;
}

#userscomments .form-group select,.form-group textarea{
    /* background-color: rgb(255, 255, 255); */
    background-color: rgb(249, 249, 249);
    /* border: 1px solid #ecb21f; */
    color: #000;
    /* border-radius: 12px; */
}

#userscomments form{
    /* border: 1px solid rgba(16, 46, 46, 1); */
    /* background-color: #d3d3d3; */
    /* background-color: rgb(249, 249, 249); */
    border-radius: 5px;
    padding: 20px;
 }


